
.skills {
    background-color: #181819;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 100px 0px;
    position: relative;


}

.wrapper {
    width: 80%;
    height: 70%;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.icon {
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    padding: 0;
    font-size: 16px;
    letter-spacing: 1px;

}


.skillTitle span {
    font: 700 40px/1.2 'Josefin Sans', sans-serif;
    justify-content: center;
    align-items: center;
    display: flex;
    padding: 0px 0px 20px 0px;
    font-size: 35px;
    box-sizing: border-box;
    letter-spacing: 5px;
    position: relative;
    font-weight: 600;
    color: #fff;
    text-transform: capitalize;
}

.skillsWrapper {
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-wrap: wrap;
    padding-top: 3px;
}


.icon {
    background-image: url('../s8_assets/react.png');
    width: 120px;
    height: 120px;

}

.icon1 {
    background-image: url(../s8_assets/HTML.png);
    width: 120px;
    height: 120px;
}

.icon2 {
    background-image: url(../s8_assets/CSS.png);
    width: 120px;
    height: 120px;
}

.icon3 {
    background-image: url(../s8_assets/TypeScr.svg);
    width: 120px;
    height: 120px;
}

.icon4 {
    background-image: url(../s8_assets/JS.png);
    width: 120px;
    height: 120px;
}

.icon5 {
    background-image: url(../s8_assets/react.png);
    width: 120px;
    height: 120px;
}

.paddings_one {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 30px;
}

@media (max-width: 1249px) {
    .skillsWrapper {
        display: flex;
        align-items: center;
    }
}

@media (max-width: 1249px) {
    .skillTitle {
        display: flex;
        flex-direction: column;
        width: auto;
    }
}

.moduleLine {
    background: #60d9fa;
    width: 55px;
    height: 3px;
    margin: 30px auto;
}