.main {
    min-height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.container {
    width: 80%;
    height: 70%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    padding: 100px 0;
}

.greeting {
    display: flex;
    flex-direction: column;
    color: white;
    padding-bottom: 50px;
}

.greeting span:first-child {
    font-weight: 400;
    font-size: 25px;
    font-family: Poppins serif;
}

.greeting span:nth-child(2),
.greeting span:nth-child(3) span {
    font-weight: 700;
    font-size: 30px;
    font-family: Josefin Sans, sans-serif;
    letter-spacing: 5px;
}

.greeting span:nth-child(2),
.greeting span:nth-child(2) span {
    font-weight: 700;
    font-stretch: normal;
    font-size: 30px;
    line-height: 1.2;
    font-family: Josefin Sans, sans-serif;
    position: relative;
    z-index: 0;

}

.greeting span:nth-child(2) span:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: -5px;
    height: 15px;
    width: 100%;
    z-index: -4;
    background: #ea548d;
}

.greetingIam {
    display: flex;
    justify-content: center;
}

.title {
    width: 22ch;
    color: #5ec5e1;
    font-size: 23px;
    font-family: Consolas, 'Liberation Mono', Menlo, Courier, monospace;
    white-space: nowrap;
    overflow: hidden;
    border-right: 4px solid #5ec5e1;
    animation: flashing-border 0.75s step-end infinite,
    printed-text 5s steps(22);
}

@keyframes flashing-border {
    0% {
        border-color: #5ec5e1;
    }
    50% {
        border-color: transparent;
    }
    100% {
        border-color: #5ec5e1;
    }
}

@keyframes printed-text {
    from {
        width: 0;
    }
}

.mainPhoto {
    position: relative;
    z-index: 1;
    margin-top: 50px;
    transition: 500ms;
    transition-timing-function: ease;
}

.mainPhoto:hover {
    transform: scale(1.1);

}

.mainPhoto div {
    height: 400px;
    width: 300px;
    background-image: url("../s8_assets/photo.jpg");
}

.mainPhoto div:before {
    content: "";
    position: absolute;
    height: 430px;
    width: 300px;
    border: 5px solid #5ec5e1;
    left: 15px;
    top: -20px;
    z-index: -1;
}

@media screen and (max-width: 542px) {
    greeting span:nth-child(2),
    .greeting span:nth-child(2) span {
        font-weight: 700;
        font-stretch: normal;
        font-size: 28px;
        line-height: 1.2;
        font-family: Josefin Sans, sans-serif;
        position: relative;
        z-index: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 10px;
    }
}

@media screen and (max-width: 931px) {
    .greeting {
        height: 110%;
        display: flex;
        flex-direction: column;
        color: white;
        text-align: center;
        padding: 10px;
        justify-content: center;
        align-items: center;
        width: 365px;
    }
}

@media screen and (max-width: 1190px) {
    .greeting span:nth-child(2) span:before {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        height: 0;
        width: 0;
        z-index: -4;
        background: #5ec5e1;
    }
}

@media screen and (max-width: 400px) {
    .mainPhoto {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
