.skillBox {
    width: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.icon {
    width: 80px;
    height: 80px;
}

.skillTitle {
    text-align: center;
    color: rgba(31, 31, 32, 0.09);
    color: white;
    font-family: Josefin Sans, sans-serif;
    display: flex;
    padding: 35px 0px 0px 0px;
    font-size: 16px;
    letter-spacing: 2px;
}

.skillDescription {
    margin: 40px 1px 1px 1px;
    font-weight: bold;
    font-family: Josefin Sans, sans-serif;
    text-align: center;
    word-wrap: inherit;
    color: #b2b2b5;
}

