
.contacts {
    min-height: 90vh;
    background-color: #252527;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}
.contactSpan{
    font-family: Josefin Sans, sans-serif;
    font-size: 30px;
}
.container {
    width: 80%;
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    font-family: Josefin Sans, sans-serif
}

.container .form {
    display: flex;
    flex-direction: column;
  justify-content: space-around;
    }

@media(max-width: 620px) {
    .conteiner .form {
        justify-content: center;
    }
}

.container .button:after{
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    height: 100%;
    width: 0%;
    background: #f47a65;
    z-index: -1;
    -webkit-transition: all 0.3s ease-in-out 0s;
    -moz-transition: all 0.3s ease-in-out 0s;
    -ms-transition: all 0.3s ease-in-out 0s;
    -o-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
    margin: 0 auto;
}

.wrapper {
    width:150px;
    margin: 5px auto;
}
.btn{
    font-family: Josefin Sans, sans-serif;
    font-size: 18px;
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    letter-spacing: 2px;
    color: #fdfdfd;
    text-align: center;
    position: relative;
    margin: 1px;
    cursor: pointer;
    background-color: #f0f8ff00;
    border: 1px solid #ff000000;
}



svg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

svg rect{
    width:100%;
    height: 100%;
    fill:transparent;
    stroke: #5ec5e1;
    stroke-width: 5px;
    stroke-dasharray: 10px 130px 20px 30px 20px 130px 20px 30px 10px;
    transition: .5s all;
}

.btn:hover rect{
    stroke-dasharray:120px 0 40px 0 120px 0 40px 0;
}

.form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 70%;
    height: 100%;
}
@media(max-width: 320px) {
     .form {
         width: 60%;
    }
}

.form input{
    width: 100%;
    margin-bottom: 2%;
    height: 5vh;
    background-color: #252527;
    border-left: none;
    border-right: none;
    border-top: none;
    border-bottom: 2px solid #60d9fa;
    outline: none;
    color: white;
    font-size: 16px;
}
.form input:hover{
    background-color: #575758;

}

.form textarea {
    height: 80%;
    width: 100%;
    background-color: #252527 !important;
    border:2px solid #60d9fa;
    border-block-end: hidden;
    border-right: hidden;
    margin-top: 35px;
    outline: none;
    color: white;
    font-size: 16px;
    line-height: 1.5;
}

.formArea {
    font-family:Josefin Sans, sans-serif;
    width: 25px;
    padding: 10px;
    resize: none;
}


.moduleLine{
    background: #60d9fa;
    width: 55px;
    height: 3px;
    margin: 30px auto;
}